.content {
	max-width: 1000px;
	/* margin-left: -20px;
	margin-right: -20px; */
	margin: 0 auto;
	margin-top: 39px;
	padding-left: 20px;
	padding-right: 20px;
}
.content-top {
	margin-bottom: 40px;
	margin-left: 20px;
	margin-right: 20px;
}
.items {
	display: flex;
	flex-wrap: wrap;
	min-height: calc(100vh - 417px);
	margin: 0 -20px;
}
.content-top {
	display: flex;
	justify-content: space-between;
}
.content-top input {
	font-size: 14px;
	width: 250px;
	border: 1px solid #f3f3f3;
	border-radius: 10px;
	padding-left: 46px;
	padding-right: 10px;
	box-sizing: border-box;
}
.no-items {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	opacity: 0.6;
	padding-bottom: 20px;
}
@media (max-width: 768px) {
	h1 {
		font-size: 24px;
	}
	.content-top input {
		font-size: 12px;
		width: 180px;
		padding-left: 25px;
		background-position: 8px 7px !important;
		background-size: 12px !important;
	}
}
@media (max-width: 470px) {
	.content-top {
		flex-direction: column;
		margin-bottom: 25px;
	}
	.content-top input {
		height: 30px;
		margin-top: 15px;
	}
}
