.overlay {
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.5);
	position: fixed;
	top: 0;
	left: 0;
}
.basket {
	width: 385px;
	height: 100vh;
	background: #fff;
	position: fixed;
	top: 0;
	right: 0;
	/* margin-right: -100%; */
	padding: 30px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	/* transition: all 0.3s ease-in 0s; */
}
h3 {
	font-weight: 700;
	font-size: 24px;
}
.basket h3 {
	margin-bottom: 30px;
}
.basket_bottom > div {
	display: flex;
	justify-content: space-between;
}
.basket_sale div {
	font-weight: 600;
}
.basket_sum div {
	font-weight: 600;
}
.basket_sum {
	margin-bottom: 24px;
}
.basket_sale {
	margin-bottom: 19px;
}
.button {
	width: 100%;
	height: 55px;
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
	background: #9dd558;
	border-radius: 18px;
	border: none;
	color: #fff;
	font-size: 16px;
	position: relative;
	transition: all 0.3s ease-in 0s;
}
.button:hover {
	background: #87c43d;
	box-shadow: 0px 14px 30px rgba(0, 0, 0, 0.05);
	transform: translateY(-3px);
}
.basket_bottom button {
	margin-bottom: 60px;
}
.basket_items {
	overflow-y: auto;
	overflow-x: hidden;
	height: calc(100vh - 305px);
	margin-bottom: 20px;
}
.basket_item {
	margin-bottom: 15px;
}

.empty-basket_content {
	height: 100%;
	text-align: center;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	margin-bottom: 80px;
	max-width: 245px;
	margin: 0 auto;
}
h4 {
	font-size: 22px;
	font-weight: 600;
}
.empty-basket_content h4 {
	margin-bottom: 9px;
	margin-top: 20px;
}
.empty-basket_detail {
	font-size: 16px;
	line-height: 24px;
	opacity: 0.4;
	margin-bottom: 40px;
}
.empty-basket_content .button {
	padding-left: 20px;
}
.button span {
	transform: rotate(180deg);
	position: absolute;
	top: 16px;
	left: 31px;
}
.success_basket {
	width: 83px;
}
.success_basket img {
	max-width: 100%;
}
.basket_items::-webkit-scrollbar {
	width: 8px;
	height: 8px;
}
.basket_items::-webkit-scrollbar-track {
	background-color: rgba(0, 0, 0, 0.4);
	border-radius: 10px;
}
.basket_items::-webkit-scrollbar-thumb {
	background-color: #dfdfdf;
	border-radius: 10px;
}
.basket-enter .basket {
	transform: translateX(385px);
}
.basket-enter {
	opacity: 0;
}
.basket-enter-active .basket {
	transform: translateX(0);
	transition: transform 0.2s ease-in 0s;
}
.basket-enter-active {
	opacity: 1;
	transition: opacity 0.2s ease-in 0s;
}
.basket-exit .basket {
	transform: translateX(0);
}
.basket-exit {
	opacity: 1;
}
.basket-exit-active .basket {
	transform: translateX(385px);
	transition: transform 0.2s ease-in 0s;
}
.basket-exit-active {
	opacity: 0;
	transition: opacity 0.2s ease-in 0s;
}

.success-basket-enter .basket {
	transform: translateX(385px);
}
.success-basket-enter {
	opacity: 0;
}
.success-basket-enter-active .basket {
	transform: translateX(0);
	transition: transform 0.3s ease-in 0s;
}
.success-basket-enter-active {
	opacity: 1;
	transition: opacity 0.2s ease-in 0s;
}
.success-basket-exit .basket {
	transform: translateX(0);
}
.success-basket-exit {
	opacity: 1;
}
.success-basket-exit-active .basket {
	transform: translateX(385px);
	transition: transform 0.2s ease-in 0s;
}
.success-basket-exit-active {
	opacity: 0;
	transition: opacity 0.2s ease-in 0s;
}

.empty-basket-enter .empty-basket_content {
	opacity: 0;
}
.empty-basket-enter-active .empty-basket_content {
	opacity: 1;
	transition: opacity 300ms ease-in;
}
.empty-basket-exit .empty-basket_content {
	opacity: 1;
}
.empty-basket-exit-active .empty-basket_content {
	opacity: 0;
	transition: opacity 300ms ease-in;
}
