.basket_item {
	display: flex;
	justify-content: space-between;
	align-items: center;
	border: 1px solid #f3f3f3;
	border-radius: 20px;
	padding: 30px 20px;
}
.basket_item-img {
	max-width: 70px;
	margin-right: 20px;
}
.basket_item-img img {
	width: 100%;
}
.basket_item-info {
	margin-right: 10px;
}
.basket_item-title {
	font-size: 14px;
	line-height: 16.94px;
	margin-bottom: 8px;
}
.basket_item-price {
	font-weight: 700;
	font-size: 14px;
}
.basket_item-delete img {
	cursor: pointer;
	transition: all 0.2s ease-in 0s;
}
.basket_item-delete:hover img {
	filter: invert(10%);
}

.basket-item-exit {
	opacity: 1;
}
.basket-item-exit-active {
	opacity: 0;
	transition: opacity 400ms ease-in;
}
