* {
	margin: 0;
	padding: 0;
}
a {
	text-decoration: none;
}
.header {
	height: 128px;
	display: flex;
	align-items: center;
	justify-content: space-between;
	max-width: 972px;
	margin: 0 auto;
	padding-right: 15px;
	padding-left: 15px;
}
.header_info {
	display: flex;
	align-items: center;
}
.header_info img {
	width: 40px;
}
.header_right {
	display: flex;
}
.header_right a,
.header_right > div {
	margin-left: 30px;
	display: flex;
	cursor: pointer;
}
.header_cart {
	display: flex;
}
.header_right a img {
	transition: all 0.2s ease-in 0s;
}
.header_right a:hover img {
	filter: invert(100%);
}
.header_cart div {
	margin-left: 10px;
	font-weight: 600;
	color: #5c5c5c;
}
h2 {
	font-weight: 700;
	font-size: 20px;
	line-height: 24.2px;
}
.header_title {
	margin-left: 16px;
}
.header_title h2 {
	color: #000;
}
.header_title div {
	font-size: 14px;
	line-height: 16.94px;
	color: #9d9d9d;
}
@media (max-width: 768px) {
	.header {
		height: 70px;
	}
	h2 {
		font-size: 13px;
		line-height: 20px;
	}
	.header_title div {
		font-size: 11px;
	}
	.header_info img {
		width: 35px;
	}
	.header_right img {
		width: 18px;
	}
}
@media (max-width: 450px) {
	.header_title {
		display: none;
	}
}
@media (max-width: 425px) {
	.header {
		height: 50px;
	}
	.header_info img {
		width: 30px;
	}
	.header_right img {
		width: 15px;
	}
	.header_right a,
	.header_right > div {
		margin-left: 15px;
	}
}
