.item {
	flex: 0 1 25%;
	padding: 0 20px;
	margin-bottom: 40px;
	box-sizing: border-box;
}
.item-content {
	border: 1px solid #f3f3f3;
	box-sizing: border-box;
	border-radius: 40px;
	padding: 20px 29px 34px;
	transition: all 0.2s ease-in 0s;
	display: flex;
	flex-direction: column;
	height: 100%;
	min-width: 180px;
}
.item-content:hover {
	box-shadow: 0px 14px 30px rgba(0, 0, 0, 0.05);
	transform: translateY(-3px);
}
.item_top {
	width: 100%;
	position: relative;
	margin-bottom: 14px;
}
.item_top img:first-child {
	width: 100%;
}

.add-favourite {
	position: absolute;
	top: 10px;
	left: 0;
	width: 30px;
	height: 30px;
	border-radius: 7px;
	border: 1px solid #f8f8f8;
	display: flex;
	align-items: center;
	justify-content: center;
}
.add-favourite.favourite-active {
	background: #fef0f0;
}
.item_like {
	max-width: 14px;
}
.item_name {
	font-size: 14px;
	line-height: 16.94px;
	margin-bottom: 14px;
	flex: 1 1 auto;
}
.item_bottom {
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.item-price label {
	font-weight: 500;
	font-size: 11px;
	line-height: 13.31px;
	color: #bdbdbd;
	text-transform: uppercase;
	margin-bottom: 2px;
}
.item-price div {
	font-weight: 700;
	font-size: 14px;
	line-height: 16.94px;
}
.item_plus {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 30px;
	height: 30px;
	border-radius: 7px;
	border: 1px solid #f8f8f8;
	cursor: pointer;
}
.item_plus.plus-active {
	background: linear-gradient(180deg, #89f09c 0%, #3cc755 100%);
}
.item_plus img {
	max-width: 11px;
}
.add-favourite {
	cursor: pointer;
}
@media (max-width: 880px) {
	.item {
		padding: 0 10px;
	}
	.item-content {
		padding: 15px 25px 20px;
	}
	.item_name {
		font-size: 12px;
		line-height: 15px;
	}
	.item-price label {
		font-size: 10px;
	}
	.item-price div {
		font-size: 12px;
	}
	.add-favourite {
		width: 25px;
		height: 25px;
	}
	.item_plus {
		width: 25px;
		height: 25px;
	}
}
@media (max-width: 800px) {
	.item {
		flex: 0 1 33.333%;
		margin-bottom: 25px;
	}
}
@media (max-width: 600px) {
	.item {
		padding: 0 8px;
	}
	.item-content {
		padding: 10px 20px 15px;
		min-width: 130px;
	}
	.item_top {
		margin-bottom: 5px;
	}
	.item_name {
		font-size: 10px;
		line-height: 14px;
		margin-bottom: 5px;
	}
	.item-price label {
		font-size: 9px;
	}
	.item-price div {
		font-size: 11px;
	}
}
@media (max-width: 440px) {
	.item {
		flex: 0 1 50%;
		margin-bottom: 10px;
	}
}
